<template>
<div id="login">
  <div class="slideImage">
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="elevation-12">
              <v-toolbar color="primary" dark flat>
              <v-toolbar-title>Login form</v-toolbar-title>
              </v-toolbar>
              <v-form>
                <v-card-text>
                  <v-alert v-if="error_messages" type="error">{{error_messages}}</v-alert>
                  <v-text-field id="email" v-model="email" label="email" name="email" type="text"/>
                  <v-text-field id="password" v-model="password" label="Password" name="password" type="password"/>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn large color="primary" to="registrate">Registrate</v-btn>
                  <v-btn large color="primary" v-on:click="login">Login</v-btn>
                </v-card-actions>
              </v-form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: "Login",
  data(){
      return {
        email : "",
        password : "",
        error_messages: null
      }
  },
  methods : {
    clearForm(){
      this.email = "",
      this.password = ""
    },
    checkForm(){
      if(!this.email || !this.password){
        this.error_messages = "You have to fill all textfield"

        return false
      }

      return true
    },
    login(){
      if(this.checkForm()){
        axios.post('http://localhost:3000/login',{
          email: this.email,
          password: this.password
        }).then(
          (response) => {
              localStorage.setItem('user', response.data.auth)
              this.$router.push('/')
          }).catch((error) => {
            this.error_messages = error.response.data.message
            this.clearForm()
          })
      }
    }
  }
}
</script>