<template>
  <div id="header">
    <v-card>
      <v-toolbar dark>
        <v-tab>RCS_SYTEM</v-tab>
        <v-tab to="/">RUNS</v-tab>
        <v-tab to="/monitor">MONITOR</v-tab>
        <v-spacer></v-spacer>
        <v-btn
          color="blue-grey"
          v-on:click="logout"
        >
          LOG OUT
        </v-btn>
      </v-toolbar>
    </v-card>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Header',
  methods: {
    logout(){
      axios.post('http://localhost:3000/logout').then(() => {
        localStorage.removeItem('user')
        this.$router.push('/login') 
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    }
  }
};
</script>