<template>
  <div class="Lamps">
    <v-data-table
        :headers="headers"
        :items="Lamps"
        sort-by="id"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Lamps</v-toolbar-title>
            <v-spacer></v-spacer>
                <v-btn color="primary" dark class="mb-2">Turn off all Lamps</v-btn>
          </v-toolbar>
        </template>
        <template v-slot:item.color="{ item }">
          <Slider v-bind:ref=item.name :value= defaultColor></Slider>
        </template>
        <template v-slot:item.flash="{ item }">
          <v-btn :disabled=isDisabled(item) color="success" @click="flashItem(item)">FLASH</v-btn>
        </template>
        <template v-slot:item.disconnect="{ item }">
          <v-btn v-if="conected(item)" color="error" @click="switchItemStatus(item)">DISABLED</v-btn>
          <v-btn v-else color="success" @click="switchItemStatus(item)">ENABLED</v-btn>
        </template>
        <template v-slot:item.turnoff="{ item }">
          <v-btn :disabled=isDisabled(item) color="primary" @click="TurnoffLamp(item)">Turn off</v-btn>
        </template>
      </v-data-table>
  </div>
</template>

<script>
import Slider from "vue-color/src/components/Slider"
import axios from 'axios'

export default {
  name: 'Lamps',
  components : {
      Slider
  },
  data() {
    return {
      disabled: false,
      headers: [
        { text: 'Id', value: 'id' },
        { text: 'Name', value: 'name' },
        { text: 'Status', value: 'status' },
        { text: 'Connected', value:'available'},
        { text: 'color', value: 'color', sortable: false},
        { text: 'Flash', value: 'flash', sortable: false },
        { text: 'Use', value: 'disconnect', sortable: false },
        { text: 'Turnoff', value: 'turnoff', sortable: false }
      ],
      Lamps:[],
      defaultColor: { r: 255, g: 255, b: 255},
    }
  },
  created(){
    this.setupStream()
    this.initLamps()
  },
  methods: {
    TurnoffAllLamps(){
      axios.post('http://localhost:3000/runs/TurnoffAllLamps').then(this.Lamps = []).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    TurnoffLamp(item){
      axios.post('http://localhost:3000/runs/TurnoffLamp', {name:item.name})
      .then((response) => {
        this.response = response.data.Lamps
      })
      .catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    isDisabled(item){
      if(this.disabled || !item.available){
        return true
      } else {
        return false
      }
    },
    conected(item){
      return !(item.status == 'disabled')
    },
    switchItemStatus(item){
      axios.post('http://localhost:3000/runs/switchLampStatus', {name:item.name}).then((response) => {
        this.Lamps = response.data.Lamps
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    flashItem(item){
      var rgb = this.$refs[item.name].val.rgba
      rgb = {
        r : rgb.r,
        g : rgb.g,
        b : rgb.b
      }

      this.disabled = true

      axios.post('http://localhost:3000/runs/flashLamp', {name: item.name, rgb:rgb}).then(() => {
        this.disabled = false
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    initLamps(){
      axios.post('http://localhost:3000/runs/getAllLamps').then((respons) => {
        this.Lamps = respons.data.Lamps
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    setupStream() {
      let es = new EventSource('http://localhost:3000/runs/Monitor', {withCredentials: true});

      es.addEventListener('error',() => {
        axios.post('http://localhost:3000/islogedIn').catch((error) => {
          if(error.response.status === 401){
            localStorage.removeItem('user')
            this.$router.push('/login') 
          }
        })
      })

      es.addEventListener('event',(response) => {
        var data = JSON.parse(response.data);
        if(data.Lamps){
          this.Lamps = data.Lamps
        }
      })

    }
  }
}
</script>