<template>
  <div class="tabel">
    <v-data-table
        :headers="headers"
        :items="runs"
        sort-by="id"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat color="white">
            <v-toolbar-title>Runs</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark class="mb-2" v-on="on">New Item</v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline">Add</span>
                </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.name" label="Name"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.tipe" label="Tipe"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.rounds" label="Rounds" type="number"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.time" label="Time" type="number"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                  <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:item.color="{ item }">
          <Slider v-bind:ref=item.id :value= defaultColor></Slider>
        </template>
        <template v-slot:item.edit="{ item }">
          <v-btn color="primary" @click="editItem(item)">EDIT</v-btn>
        </template>
        <template v-slot:item.start="{ item }">
          <v-btn color="success" @click="start(item)">START</v-btn>
        </template>
        <template v-slot:item.delete="{ item }">
          <v-btn color="error" @click="deleteItem(item)">DELETE</v-btn>
        </template>
      </v-data-table>
  </div>
</template>

<script>
import Slider from 'vue-color/src/components/Slider'
import axios from 'axios';

export default {
  name: 'Table',
  components:{
    Slider
  },
  data() {
    return {
      dialog: false,
      headers: [
        { text: 'Id', value: 'id' },
        { text: 'Name', value: 'name' },
        { text: 'Tipe', value: 'tipe' },
        { text: 'Rounds', value: 'rounds' },
        { text: 'Time', value: 'time' },
        {text: 'color', value: 'color', sortable: false},
        { text: 'edit', value: 'edit', sortable: false },
        { text: 'start', value: 'start', sortable: false },
        { text: 'delete', value: 'delete', sortable: false }
      ],
      editedIndex: -1,
      editedItem: {
        name: null,
        tipe: null,
        rounds: null,
        time: null,
      },
      defaultItem: {
        name: null,
        tipe: null,
        rounds: null,
        time: null,
      },
      defaultColor:{ r: 255, g: 255, b: 255},
      runs:[]
    }
  },
  created(){
    this.getAll()
  },
  methods: {
    getAll(){
      axios.post('http://localhost:3000/runs/getAllRuns').then((response) => {
        this.runs = response.data.runs
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    start(item){
      var rgb = this.$refs[item.id].val.rgba
      rgb = {
        r : rgb.r,
        g : rgb.g,
        b : rgb.b
      }

      axios.post('http://localhost:3000/runs/startRun',{id:item.id,rgb:rgb})
      .catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    close(){
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save(){
      if (this.editedIndex > -1) {
        axios.post('http://localhost:3000/runs/editRun',{
          id: this.runs[this.editedIndex].id,
          run:this.editedItem
        }).then(() => {
          this.getAll()
        }).catch((error) => {
          if(error.response.status === 401){
            localStorage.removeItem('user')
            this.$router.push('/login') 
          }
        })
      } else {
        axios.post('http://localhost:3000/runs/addRun',{
          run:this.editedItem
        }).then(() => {
          this.getAll()
        }).catch((error) => {
          if(error.response.status === 401){
            localStorage.removeItem('user')
            this.$router.push('/login') 
          }
        })
      }
      this.close()
    },
    deleteItem(item) {
      axios.post('http://localhost:3000/runs/deleteRun',{id:item.id}).then(() => {
        this.runs.splice(this.runs.indexOf(item), 1)
      }).catch((error) => {
        if(error.response.status === 401){
          localStorage.removeItem('user')
          this.$router.push('/login') 
        }
      })
    },
    editItem (item) {
      this.editedIndex = this.runs.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
  }
}
</script>