<template>
<div id="registrate">
  <div class="slideImage">
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
              <v-toolbar-title>Login form</v-toolbar-title>
              </v-toolbar>
              <form>
                <v-card-text>
                  <v-alert v-if="success_messages" type="success">{{success_messages}}</v-alert>
                  <v-alert v-if="error_messages" type="error">{{error_messages}}</v-alert>
                  <v-text-field id="email" v-model="email" label="email" name="email" type="text"/>
                  <v-text-field id="name" v-model="name" label="name" name="name" type="text"/>
                  <v-text-field id="password" v-model="password" label="Password" name="password" type="password"/>
                  <v-text-field id="passwordAgain" v-model="passwordAgain" label="password again" name="passwordAgain" type="password"/>
                  <v-checkbox id="directives" v-model="directives" class="mx-2" label="Aception of directives"></v-checkbox>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn large color="primary" v-on:click="registrate">registrate</v-btn>
                  <v-btn large color="primary" to="login">Login</v-btn>
                </v-card-actions>
              </form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
  </div>
</div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Registrate',
  data() {
    return {
      email : "",
      name: "",
      password: "",
      passwordAgain: "",
      directives: false,
      error_messages: null,
      success_messages: null
    }
  },

  methods: {
    checkForm() {
      if(!this.email || !this.password || !this.name){
        this.error_messages = "You have to fill all textfield"

        return false
      }

      if(this.password != this.passwordAgain){
        this.error_messages = "Passwords not match"
        return false
      }

      if(!this.directives){
        this.error_messages = "You have to accept directives"
        return false
      }

      this.success_messages = ""
      this.error_messages = ""

      return true
    },
    clearForm(){
      this.email = ""
      this.name = ""
      this.password = ""
      this.passwordAgain = ""
      this.directives = false
    },
    registrate() {
        if(this.checkForm()){
          axios
          .post('http://localhost:3000/registrate',
          {
            email: this.email,
            name: this.name,
            password: this.password
          }).then((response) => {
            if(response.data.message){
              this.success_messages = response.data.message
              this.clearForm()
            }
          }).catch((error) => {
            this.error_messages = error.response.data.message
            this.clearForm()
          })
        }
    }
  }
};
</script>

